<style>
#about-close-button {
  width: 20px;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  z-index: 7;
}

#about-page {
  padding-left: 20px;
  padding-top: 10px;
  height: 100%;
  width: 100%;
  text-align: left;
  position: relative;
}
#about-page-container {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
}
#about-page-container p {
  margin: 0;
}
#about-title-container {
  margin: 0;
  font-size: 1.35em;
}
#about-statement-container p {
  font-size: 0.9em;
  line-height: 1.7;
  width: 60%;
  margin-top: 20px;
  margin-bottom: 20px;
  word-break: keep-all;
}
.close-button-animation {
  -webkit-transition: -webkit-transform 0.4s ease-in-out;
  -ms-transition: -ms-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
}
.close-button-animation:hover {
  transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
}
</style>



